<template>
<v-container>
 <v-toolbar flat color="white">
   <v-toolbar-title>Crud Atma pedia</v-toolbar-title>
   <v-divider class="mx-4" inset vertical></v-divider>
   
   <v-dialog v-model="dialog" max-width="500px">
    <template v-slot:activator="{ on }">
     <v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
    </template>
    <v-card>
     <v-card-title>
     <span class="headline">{{ formTitle }}</span>
     </v-card-title>
     <v-card-text>
      <v-container>
       <v-row>
        <v-col cols="12" sm="6" md="4">
        <v-text-field v-model="editedItem.name" label="Drink name"></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" md="4">
            <v-select v-model="editedItem.calories" :items="items" label="Categories"></v-select>
        </v-col>
        <v-col cols="12" sm="6" md="4">
            <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" md="4">
            <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" md="4">
            <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
        </v-col>
       </v-row>
      </v-container>
     </v-card-text>
     <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
      <v-btn color="blue darken-1" text @click="save">Save</v-btn>
     </v-card-actions>
    </v-card>
   </v-dialog>
  </v-toolbar>
  
 <v-row>
 <v-col cols="3" v-for="drinkens in drinkens" :key="drinkens.name">
 <v-card
    max-width="344"
    outlined
 class="d-inline-flex" 
 
  >
    <v-list-item three-line>
        <v-list-item-content>
            <div class="overline mb-4">MINUMAN</div>
            <v-list-item-title class="headline mb-1">{{drinkens.name}}</v-list-item-title>
            <v-list-item-subtitle>Categories: {{drinkens.calories}}</v-list-item-subtitle>
            <v-list-item-subtitle>Fat: {{drinkens.fat}}</v-list-item-subtitle>
            <v-list-item-subtitle>Carbs: {{drinkens.carbs}}</v-list-item-subtitle>
            <v-list-item-subtitle>Protein: {{drinkens.protein}}</v-list-item-subtitle>
            </v-list-item-content>
    </v-list-item>

    <v-card-actions>
      <v-icon class="mr-1" color="teal" @click="editItem(drinkens)">mdi-silverware</v-icon>
  <v-icon class="mr-1" color="red"  @click="deleteItem(drinkens)">mdi-delete</v-icon>
    </v-card-actions>
  </v-card>
  </v-col>
</v-row>
</v-container>
</template>

<script>
import { drinkensRef } from '../firebase';
  export default {
    data: () => ({
      items: ['Hangat', 'Es'],
      cek: -1,
      dialog: false,
      selected: [],
      search: '',
      headers: [
        {
          text: 'Dessert (100g serving)',
          align: 'left',
          sortable: false,
          value: 'name',
        },
        { text: 'Category', value: 'calories' },
        { text: 'Fat (g)', value: 'fat' },
        { text: 'Carbs (g)', value: 'carbs' },
        { text: 'Protein (g)', value: 'protein' },
        { text: 'Actions', value: 'action', sortable: false },
        { text: "Select", value: "select", sortable:false }
      ],
      drinkens: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),

    firebase: {
      drinkens : drinkensRef
    },

    computed: {
      formTitle () {
        return this.cek === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    methods: {
      clear(){
        this.editedItem={}
        this.editedItem.name=''
        this.editedItem.calories=0
        this.editedItem.fat=0
        this.editedItem.carbs=0
        this.editedItem.protein=0
        this.cek=-1
      },
      editItem (item) {
        this.editedIndex = item['.key']
        this.cek = 0
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        confirm('Are you sure you want to delete this item?') && drinkensRef.child((item['.key'])).remove()
      },

      deleteMultiple(){   
        for(let i=0; i < this.selected.length ; i++)
        {
            drinkensRef.child(this.selected[i]).remove()
        }
        this.selected = [];   
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if(this.cek>-1){
            drinkensRef.child(this.editedIndex).set({name:this.editedItem.name,calories:this.editedItem.calories
            ,fat:this.editedItem.fat,carbs:this.editedItem.carbs,protein:this.editedItem.protein})
            this.clear()
          }else{
            drinkensRef.push({name:this.editedItem.name,calories:this.editedItem.calories,
            fat:this.editedItem.fat,carbs:this.editedItem.carbs,protein:this.editedItem.protein})
            this.clear()
          }
          this.close()
      },
    },
  }
</script>